import { editingFields, IEditingResourceState } from "../../common/store/editingEntitySlice";
import { 
  AbonementType, 
  IAbonementOffer, 
  id, 
  ILesson, 
  IRule, 
  ISubject, 
  LessonsPreviewSource, 
  PeriodicityType,
} from "../../common/types/domain";
import { ISubjectView, ITeacherView } from "../../common/types/teacherDomainView";
import { ArgumentSelector } from "../../common/types/utility";

import { selectExternalLessonsData } from "./external";
import { selectTeacherViewSubjects } from "./domain";
import { TeacherSelector } from "./types";
import { selectCurrentDate } from "./novigation";
import { 
  getPreviewLessonsFromSubjects,
  getPreviewLessonsFromASubject,
  getPreviewLessonsFromASingleRule,
  isIntersect
} from "../../common/utilities/RulePreviewCalendarFunctions/getPreviewLessons";

const selectAbonementOffer: TeacherSelector<IEditingResourceState<IAbonementOffer>> = (state) => state.editableAbonementOffer;
export const selectAbonementOfferData: TeacherSelector<IAbonementOffer> = (state) => selectAbonementOffer(state).data;
export const selectAbonementOfferLimitLessons: TeacherSelector<number> = (state) => selectAbonementOfferData(state).limitLessons; 
export const selectAbonementOfferPrice: TeacherSelector<number> = (state) => selectAbonementOfferData(state).price; 
export const selectAbonementOfferAbonementType: TeacherSelector<AbonementType> = (state) => selectAbonementOfferData(state).type;
export const selectAbonementOfferAbonementSubjects: TeacherSelector<id[]> = (state) => selectAbonementOfferData(state).subjects;
export const selectAbonementOfferLimitTime: TeacherSelector<number> = (state) => selectAbonementOfferData(state).limitTime; 

const selectRule: TeacherSelector<IEditingResourceState<IRule>> = (state) => state.editableRule;
export const selectRuleData: TeacherSelector<IRule> = (state) => selectRule(state).data;
export const selectRuleDuration: TeacherSelector<number> = (state) => selectRuleData(state).durationMin;
export const selectRulePeriodicityType: TeacherSelector<PeriodicityType> = (state) => selectRuleData(state).periodicity.periodicityType;  
export const selectRuleStartTime: TeacherSelector<Date> = (state) => selectRuleData(state).timeStart;
export const selectRulePeriodicityRule: TeacherSelector<number[]> = (state) => selectRuleData(state).periodicity.periodicityRule;

const selectSubject: TeacherSelector<IEditingResourceState<ISubjectView>> = (state) => state.editableSubject;
export const selectSubjectData: TeacherSelector<ISubjectView> = (state) => selectSubject(state).data;
const selectSubjectEditingFields: TeacherSelector<editingFields> = (state) => selectSubject(state).fieldsEditing;

const isSubjectFieldEditingSelectorCreator: ArgumentSelector<TeacherSelector<boolean>> = (fieldName: string) => {
  return (state) => selectSubjectEditingFields(state)[fieldName];
}

export const selectIsSubjectNameEditing = isSubjectFieldEditingSelectorCreator('name');
export const selectIsSubjectDescriptionEditing = isSubjectFieldEditingSelectorCreator('description');
export const selectSubjectName: TeacherSelector<string> = (state) => selectSubjectData(state).name;
export const selectSubjectDescription: TeacherSelector<string> = (state) => selectSubjectData(state).description;

const selectTeacher: TeacherSelector<IEditingResourceState<ITeacherView>> = (state) => state.editableTeacher;
const selectTeacherData: TeacherSelector<ITeacherView> = (state) => selectTeacher(state).data;
const selectTeacherEditingFields: TeacherSelector<editingFields> = (state) => selectTeacher(state).fieldsEditing;

const isTeacherFieldEditingSelectorCreator: ArgumentSelector<TeacherSelector<boolean>> = (fieldName: string) => {
  return (state) => selectTeacherEditingFields(state)[fieldName];
}

export const selectIsTeacherNameEditing: TeacherSelector<boolean> = isTeacherFieldEditingSelectorCreator('name');
export const selectIsTeacherDescriptionEditing: TeacherSelector<boolean> = isTeacherFieldEditingSelectorCreator('description');
export const selectTeacherName: TeacherSelector<string> = (state) => selectTeacherData(state).name;
export const selectTeacherDescription: TeacherSelector<string> = (state) => selectTeacherData(state).description;

//may need to be moved to domain
export const selectSubjectId: TeacherSelector<id> = (state) => selectSubjectData(state).id;
export const selectRuleId: TeacherSelector<id> = (state) => selectRuleData(state).id;
export const selectAbonementOfferId: TeacherSelector<id> = (state) => selectAbonementOfferData(state).id;
export const selectSubjectRules: TeacherSelector<IRule[]> = (state) => selectSubjectData(state).rules;
export const selectTeacherSubjects: TeacherSelector<ISubjectView[]> = (state) => selectTeacherData(state).subjects;
export const selectTeacherAbonementOffers: TeacherSelector<IAbonementOffer[]> = (state) => selectTeacherData(state).abonementOffers;

export const selectSubjectLessons: TeacherSelector<ILesson[]> = (state) => {
  const subjectId = selectSubjectId(state);
  const externalLessons = selectExternalLessonsData(state);
  const subjectLessons = externalLessons.filter(lesson => lesson.subject.id === subjectId);
  return subjectLessons;
}

export const selectTeacherSubjectsExceptEditing: TeacherSelector<ISubjectView[]> = (state) => {
  const subjectViews = selectTeacherViewSubjects(state);
  const subjectId = selectSubjectId(state);
  const subjectsExceptEditing = subjectViews.filter((subjectView: ISubjectView) => subjectView.id !== subjectId);
  return subjectsExceptEditing;
};

export const selectOuterPreviewLessons: TeacherSelector<ILesson[]> = (state) => {
  const currentDate = selectCurrentDate(state);
  const subjectViews = selectTeacherSubjectsExceptEditing(state);
  const outerPreviewLessons = getPreviewLessonsFromSubjects(subjectViews, LessonsPreviewSource.outerSchedule,  currentDate);
  return outerPreviewLessons; 
};

export const selectSubjectRulesExceptEditingRule: TeacherSelector<IRule[]> = (state) => {
  const subjectRules = selectSubjectRules(state);
  const ruleId = selectRuleId(state);
  const subjectRulesExceptEditingRule = subjectRules.filter(rule => rule.id !== ruleId);
  return subjectRulesExceptEditingRule;
};

export const selectSubjectExceptEditingRule: TeacherSelector<ISubjectView> = (state) => {
  const subjectView = selectSubjectData(state);
  const subjectRulesExceptEditingRule = selectSubjectRulesExceptEditingRule(state);
  const subjectExceptEditingRule = {...subjectView, rules: subjectRulesExceptEditingRule};
  return subjectExceptEditingRule;
};

export const selectSubjectPreviewLessons: TeacherSelector<ILesson[]> = (state) => {
  const currentDate = selectCurrentDate(state);
  const subjectView = selectSubjectExceptEditingRule(state);
  const subjectPreviewLessons = getPreviewLessonsFromASubject(subjectView, LessonsPreviewSource.thisSubjectSchedule,  currentDate);
  return subjectPreviewLessons; 
};

export const selectEditingRulePreviewLessons: TeacherSelector<ILesson[]> = (state) => {
  const currentDate = selectCurrentDate(state);
  const subjectView = selectSubjectData(state);
  const rule = selectRuleData(state);
  const rulePreviewLessons = getPreviewLessonsFromASingleRule(subjectView, rule, LessonsPreviewSource.editingRuleSchedule,  currentDate);
  return rulePreviewLessons; 
};

export const selectPreviewSchedule: TeacherSelector<ILesson[]> = (state) => {
  const outerPreviewLessons = selectOuterPreviewLessons(state);
  const subjectPreviewLessons = selectSubjectPreviewLessons(state);
  const editingRulePreviewLessons = selectEditingRulePreviewLessons(state);
  const previewSchedule = [...outerPreviewLessons, ...subjectPreviewLessons, ...editingRulePreviewLessons];
  return previewSchedule;
};

export const selectEditingRulePreviewHasIntersections: TeacherSelector<boolean> = (state) => {
  const outerPreviewLessons = selectOuterPreviewLessons(state);
  const subjectPreviewLessons = selectSubjectPreviewLessons(state);
  const editingRulePreviewLessons = selectEditingRulePreviewLessons(state);
  const previewSchedule = [...outerPreviewLessons, ...subjectPreviewLessons];

  const isScheduleIntersect = isIntersect(editingRulePreviewLessons, previewSchedule);
  return isScheduleIntersect;
};

export const selectEditingSubjectAsISubject: TeacherSelector<ISubject> = (state) => {
  const subject: ISubjectView = selectSubjectData(state);
  const ruleIds: id[] = selectSubjectRuleIds(state);
  const subjectWithRuleIds: ISubject = {...subject, rules: ruleIds};
  return subjectWithRuleIds;
};

export const selectSubjectRuleIds: TeacherSelector<id[]> = (state) => {
  const subject: ISubjectView = selectSubjectData(state);
  const ruleIds: id[] = subject.rules?.map((rule) => rule.id) || [];
  return ruleIds;
};