import { put, select } from "redux-saga/effects";
import { createCommand } from "../../../common/utilities/createCommand";
import { selectIsSubjectShowMore, selectIsTeacherShowMore, selectRuleModalMode } from "../../selectors/novigation";
import { 
  LessonView, 
  SubjectListView, 
  teacherModalSlice, 
  TeacherView, 
  TeacherCalendarRepresentationType 
} from "../../store/modal";
import { 
  resetEditableAbonementOfferStateEffect, 
  resetEditableRuleStateEffect, 
  resetEditableSubjectStateEffect, 
  resetModalStateEffect
} from "../domain/updateDomainEffect";
import { closeAbonementModalEffect, closeNewRuleModalEffect, closeSubjectPageEffect } from "./effects";
import { ModalMode } from "../../../common/component/types";

export const setTecherCalendarRepresentationType = createCommand<TeacherCalendarRepresentationType>(
  'setTecherCalendarRepresentationTypeCalendarWeek',
  function*({payload}) {
    yield put(teacherModalSlice.actions.setModalState({techerCalendarRepresentationType: payload}));
  }
);

export const navigateSchedule = createCommand<void>(
  'navigateSchedule',
  function*() {
    yield resetModalStateEffect();
    yield put(teacherModalSlice.actions.setModalState({teacherView: TeacherView.schedule}));
  }
);

export const navigateSubjectList = createCommand<void>(
  'navigateSubjectList',
  function*() {
    yield resetModalStateEffect();
    yield put(teacherModalSlice.actions.setModalState({teacherView: TeacherView.subjectList}));
  }
);

export const subcectListViewSubjects = createCommand<void>(
  'subcectListViewSubjects',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({subjectListView: SubjectListView.subjects}));
  }
);

export const subjectListViewAbonements = createCommand<void>(
  'subjectListViewAbonements',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({subjectListView: SubjectListView.abonements}));
  }
);

export const cancelModifySubject = createCommand<void>(
  'cancelModifySubject',
  function*() {
    yield resetModalStateEffect();
    yield closeSubjectPageEffect();
    yield resetEditableSubjectStateEffect();  }
);

export const startCreatingSubject = createCommand<void>(
  'startCreatingSubject',
  function*() {
    yield put (setTecherCalendarRepresentationType.action(TeacherCalendarRepresentationType.calendarWeek));
    
    yield put(teacherModalSlice.actions.setModalState({teacherView: TeacherView.subjectPage}));
  }
);

export const startCreatingAbonements = createCommand<void>(
  'startCreatingAbonements',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({isAbonementModalOpen: true}));
  }
);

export const closeAbonementModal = createCommand<void>(
  'closeAbonementModal',
  function*() {
    yield closeAbonementModalEffect();
    yield resetEditableAbonementOfferStateEffect();
  }
);

export const subjectShowMore = createCommand<void>(
  'subjectShowMore',
  function*() {
    const isSunjectShowMore: boolean = yield select(selectIsSubjectShowMore);
    yield put(teacherModalSlice.actions.setModalState({subjectDescriptionShowMore:!isSunjectShowMore}));
  }
);

export const teacherShowMore = createCommand<void>(
  'teacherShowMore',
  function*() {
    const isTeacherShowMore: boolean = yield select(selectIsTeacherShowMore);
    yield put(teacherModalSlice.actions.setModalState({teacherDescriptionShowMore:!isTeacherShowMore}));
  }
);

export const startCreatingRule = createCommand<void>(
  'startCreatingRule',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({isNewRuleModalOpen:true}));
  }
);

export const toggleRuleModalMode = createCommand<void>(
  'toggleRuleModalMode',
  function*() {
    const modalMode: ModalMode = yield select(selectRuleModalMode);
    if (modalMode === ModalMode.halfHeight) {
      yield put(teacherModalSlice.actions.setModalState({ruleModalMode:ModalMode.fillHeight}));
    } else {
      yield put(teacherModalSlice.actions.setModalState({ruleModalMode:ModalMode.halfHeight}));
    }
  }
);

export const cancelModifyRule = createCommand<void>(
  'cancelModifyRule',
  function*() {
    yield closeNewRuleModalEffect();
    yield resetEditableRuleStateEffect();
  }
);

export const lessonAcceptedView = createCommand<void>(
  'lessonAcceptedView',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({lessonView: LessonView.accepted}));
  }
);

export const lessonPendingView = createCommand<void>(
  'lessonPendingView',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({lessonView: LessonView.pending}));
  }
);

export const lessonDiclainedView = createCommand<void>(
  'lessonDiclainedView',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({lessonView: LessonView.declained}));
  }
);

export const openLessonAddParticipiantModal = createCommand<void>(
  'openLessonAddParticipiantModal',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({isLessonAddParticipantModalOpen: true}));
  }
);

export const closeLessonAddParticipiantModal = createCommand<void>(
  'closeLessonAddParticipiantModal',
  function*() {
    yield put(teacherModalSlice.actions.setModalState({isLessonAddParticipantModalOpen: false}));
  }
);
