import { call, put, select } from "redux-saga/effects";
import { IExternalResourceFilters } from "../../../common/store/externalResourceSlice";
import { ILesson, ITeacher, IUser, id } from "../../../common/types/domain";
import { ISubjectView } from "../../../common/types/teacherDomainView";
import { selectExternalUsersFilters } from "../../selectors/external";
import { 
  listExternalLessonsApiCall, 
  listExternalStudentsApiCall, 
  listExternalSubjectsApiCall, 
  listExternalTeachersApiCall 
} from "./externalResourcesApi";
import { externalLessonsSlice } from "../../store/teacherExternalResources";
import { selectAuthenticatedUserId } from "../../../common/selectors/commonSelectors";

export function* listExternalStudentsEffect() {

  const filters: IExternalResourceFilters = yield select(selectExternalUsersFilters);
  const data: IUser[] = yield call(listExternalStudentsApiCall, filters);
  return data;
}

export function* listExternalTeachersEffect() {
  const userId: id = yield select(selectAuthenticatedUserId);
  const filters: IExternalResourceFilters = yield select(selectExternalUsersFilters);
  const data: ITeacher[] = yield call(listExternalTeachersApiCall, userId, filters);
  return data;
}

export function* listExternalLessonsEffect() {
  const userId: id = yield select(selectAuthenticatedUserId);
  const filters: IExternalResourceFilters = yield select(selectExternalUsersFilters);
  const data: ILesson[] = yield call(listExternalLessonsApiCall, userId, filters);
  yield put(externalLessonsSlice.actions.setResourses(data));
}

export function* listExternalSubjectsEffect() {
  const userId: id = yield select(selectAuthenticatedUserId);
  const filters: IExternalResourceFilters = yield select(selectExternalUsersFilters);
  const data: ISubjectView[] = yield call(listExternalSubjectsApiCall, userId, filters);
  return data;
}
