import { Typography } from '@mui/material';
import React, { useRef, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { 
    selectTeacherOffersLength, 
    selectTeacherSubjectsLength 
} from '../../selectors/domain';
import { useTeacherSelector } from '../../store';
import { selectUserProfilePhotoUrl } from '../../../common/selectors/commonSelectors';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { 
    changeTeacherDescription, 
    confirmChangeTeacherDescription, 
    startChangeTeacherDescription 
} from '../../commands/editable/editable';
import { selectIsTeacherDescriptionEditing, selectTeacherDescription } from '../../selectors/editable';
import { teacherShowMore } from '../../commands/navigation/navigation';
import { selectIsTeacherShowMore } from '../../selectors/novigation';
import { 
    avatarHeight, 
    descriptionDefaultHeight, 
    photoHeight, 
    selectTeacherDescriptionHeight
} from '../../selectors/dom';
import { Avatar } from '../../../common/component/Avatar';
import { ClickOutsideHandler } from '../../../common/component/ClickOutsideHandler';
import { TextField, TextFieldType } from '../../../common/component/TextField';
import { TextButton } from '../../../common/component/TextButton';
import { DescriptionContainer, MetricItemContainer, MetricsContainer, ProfileWrapper } from '../../../common/component/MetricStyled';

export const TeacherProfile: React.FC = () => {
    const subjectsNumber = useTeacherSelector(selectTeacherSubjectsLength);
    const offersNumber = useTeacherSelector(selectTeacherOffersLength);
    const photoUrl = useTeacherSelector(selectUserProfilePhotoUrl);
    const profileDescription = useTeacherSelector(selectTeacherDescription);
    const isDescriptionEditing = useTeacherSelector(selectIsTeacherDescriptionEditing);
    const showMore = useTeacherSelector(selectIsTeacherShowMore);
    const descriptionHeight = useTeacherSelector(selectTeacherDescriptionHeight);


    const dispatch = useDispatch();

    const descriptionContainerRef = useRef<HTMLDivElement | null>(null);
    const [hasShowMore, setHasShowMore] = React.useState(false);

    useEffect(() => {
        if (descriptionContainerRef.current) {
          const container = descriptionContainerRef.current;
          const contentHeight = container.scrollHeight;
      
          setHasShowMore( contentHeight > descriptionDefaultHeight);
          container.scrollTop = 0;
        }
      }, [profileDescription, showMore, isDescriptionEditing]);

    return (
        <ProfileWrapper minHeight={photoHeight + descriptionHeight}>
            <MetricsContainer height={photoHeight}>
                <MetricItemContainer>
                    <Avatar image={photoUrl} size={avatarHeight} onClick={() => undefined} />
                </MetricItemContainer>
                <MetricItemContainer>
                    <Typography textAlign="center">{subjectsNumber}</Typography>
                    <Typography textAlign="center">Subjects</Typography>
                </MetricItemContainer>
                <MetricItemContainer>
                    <Typography textAlign="center">{offersNumber}</Typography>
                    <Typography textAlign="center">Abonements</Typography>
                </MetricItemContainer>
            </MetricsContainer>
            <ClickOutsideHandler onClickOutside={() => dispatch(confirmChangeTeacherDescription.action())}>
                <DescriptionContainer
                    ref={descriptionContainerRef}
                    height={descriptionHeight}
                    maxHeight={ descriptionHeight }
                    overflow={ showMore ? "auto" : "hidden" }
                    onClick={ () => dispatch(startChangeTeacherDescription.action()) }
                >
                    {isDescriptionEditing ? 
                        <TextField 
                            type={TextFieldType.textArea}
                            text={profileDescription}
                            onChange={(e) => dispatch(changeTeacherDescription.action(e))}
                        /> :
                        <Typography
                        style={{ wordWrap: 'break-word' }}
                        gutterBottom
                        onClick={()=>dispatch(startChangeTeacherDescription.action())}
                        >
                            {profileDescription ? profileDescription : 'add your description'}
                        </Typography>
                    }
                </DescriptionContainer>
            </ClickOutsideHandler>
            {(hasShowMore && !isDescriptionEditing) && (
                <TextButton
                    title='See more'
                    onClick={() => dispatch(teacherShowMore.action())}
                >
                    { showMore ? <KeyboardArrowUpIcon  /> : <KeyboardArrowDownIcon /> }
                </TextButton>
            )}
        </ProfileWrapper>
    );
}
