import { createEditingSlice } from "../../common/store/editingEntitySlice";
import { 
  AbonementType, 
  IAbonementOffer, 
  IRule, 
  PeriodicityType 
} from "../../common/types/domain";
import { ISubjectView, ITeacherView } from "../../common/types/teacherDomainView";

export const editableTeacherSlice = createEditingSlice<ITeacherView>({
  id: '',
  name: '',
  abonementOffers: [],
  subjects: [],
  description: '',
  avatar: ''
}, 'editingTeacher');

export const editableSubjectSlice = createEditingSlice<ISubjectView>({
  id: '',
  name: '',
  description: '',
  rules: [],
  teacherId: '',
}, 'editingSubject');

export const editableAbonementOfferSlice = createEditingSlice<IAbonementOffer>({
  id: '',
  type: AbonementType.limited,
  subjects: [],
  limitTime: 0,
  limitLessons: 0,
  price: 0,
  teacherId: '',
}, 'editingAbonementOffer');

export const editableRuleSlice = createEditingSlice<IRule>({
  id: '',
  timeStart: new Date(),
  durationMin: 0,
  periodicity: {
    periodicityType: PeriodicityType.weekly,
    periodicityRule: [],
  },
  teacherId: '',
  subjectId: '',

}, 'editableRule');
