import { put, select } from "redux-saga/effects";
import { selectCurrentDate } from "../../selectors/novigation";
import { addDays, subDays } from "date-fns";
import { externalLessonsSlice } from "../../store/teacherExternalResources";


export function* updateExternalLessonsDateRangeEffect () {
  const currentDate: Date = yield select(selectCurrentDate);

  const startDate = subDays(currentDate, 7);

  const endDate = addDays(currentDate, 7);

  yield put(externalLessonsSlice.actions.setFilters({startDate: startDate, endDate: endDate}));
}
