import { call } from "redux-saga/effects";
import { ITeacher } from "../../../common/types/domain";
import { FirebaseUserInfo } from "../../../common/types/utility";
import { domainHandelers } from "../../../common/api/domainApi";
import { User } from "firebase/auth";
import { mapFirebaseUser } from "../../../common/utilities/mapFirebaseUser";

export const createNewUserFromUserEffect = function*(user: User) {
  const firebaseUserInfo = mapFirebaseUser(user.providerData[0]);
  yield call(createNewTeacherEffect, firebaseUserInfo);
};

export const createNewTeacherEffect = function*({ id, displayName, photoURL }: FirebaseUserInfo) {
  if (id) {
    const currentTeacher: ITeacher = yield call(domainHandelers.teacherAPIHandeler.single, id);
    const isExistingTeacher = currentTeacher && currentTeacher.name && currentTeacher.avatar && currentTeacher.abonementOffers;
    if (!isExistingTeacher) {
      const currentUserTeacher: ITeacher = {
        id,
        name: displayName || '',
        description: '',
        avatar: photoURL || '',
        subjects: [],
        abonementOffers: [],
      }
      yield call(domainHandelers.teacherAPIHandeler.update, currentUserTeacher);
    }
  }
}
