import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ITeacherView } from '../../../common/types/teacherDomainView'

const initialDomainView: ITeacherView = {
  id: '',
  name: 'Harry Potter',
  avatar: '',
  description: '',
  subjects: [],
  abonementOffers: [],
}

export const teacherDomainViewSlice = createSlice({
  name: 'teacherDomainView',
  initialState: initialDomainView,
  reducers: {
    setDomainView: (state, {payload}: PayloadAction<ITeacherView>) => ({...payload}),
    resetDomainView: () => ({...initialDomainView}),
  },
});
