import { put, select } from "redux-saga/effects";
import { id, ILesson, IUser } from "../../../common/types/domain";
import { createCommand } from "../../../common/utilities/createCommand";
import { listExternalStudentsEffect } from "../../api/external";
import { 
  fetchOwnLessonEffect, 
  removeStudentFromLessonEffect,
  cancelOwnLessonEffect,
  addStudenToLessonLessonEffect,
  finishOwnLessonEffect,
  requestStatusChangeEffect,
  sentAnotherNotificationEffect
 } from "../../api/OwnLesson";
import { lessonSlice } from "../../store/lesson";
import { teacherModalSlice, TeacherView } from "../../store/modal";
import { externalUsersSlice } from "../../store/teacherExternalResources";
import { selectCurrentDate } from "../../selectors/novigation";
import {  
  addDays, 
  subDays
} from 'date-fns';
import { updateExternalLessonsDateRangeEffect } from "./updateExternalLessonsDateRangeEffect";

export const navigateLesson = createCommand<id>(
    'navigateLesson',
    function*({payload: lessonId}) {
      const lesson: ILesson = yield fetchOwnLessonEffect(lessonId);
      console.log("lesson in navigateLesson",lesson);
      yield put(lessonSlice.actions.setLessonState(lesson));

      yield put(teacherModalSlice.actions.setModalState({teacherView: TeacherView.lessonPage}));
    }
  );

  export const removeStudentFromLesson = createCommand<id>(
    'removeStudentFromLesson',
    function*({payload: studentId}) {
      const lesson: ILesson = yield removeStudentFromLessonEffect(studentId);
      yield put(lessonSlice.actions.setLessonState(lesson));
    }
  );
  
  export const cancelLesson = createCommand<void>(
    'cancelLesson',
    function*() {
      const lesson: ILesson = yield cancelOwnLessonEffect();
      yield put(lessonSlice.actions.setLessonState(lesson));
    }
  );

  export const finalizeLesson = createCommand<void>(
    'finalizeLesson',
    function*() {
      const lesson: ILesson = yield finishOwnLessonEffect();
      yield put(lessonSlice.actions.setLessonState(lesson));
    }
  ); 
  
  export const addStudentToLesson = createCommand<id>(
    'addStudentToLesson',
    function*({payload: studentId}) {
      const lesson: ILesson = yield addStudenToLessonLessonEffect(studentId);
      yield put(lessonSlice.actions.setLessonState(lesson));    
    }
  );
  
  export const sendAnotherNotification = createCommand<id>(
    'sendAnotherNotification',
    function*({payload: studentId}) {
      yield sentAnotherNotificationEffect(studentId);
    }
  );
  
  export const requestStatusChange = createCommand<id>(
    'requestStatusChange',
    function*({payload: studentId}) {
      const lesson: ILesson = yield requestStatusChangeEffect(studentId);
      yield put(lessonSlice.actions.setLessonState(lesson));  
    }
  );
  
  export const changeAddParticipantSearch = createCommand<string>(
    'changeAddParticipantSearch',
    function*({payload: participantSearch}) {
      yield put(externalUsersSlice.actions.setFilters({search: participantSearch}));
    }
  );
  
  export const searchParticipantToAdd = createCommand<void>(
    'searchParticipantToAdd',
    function*() {
      const findedUsers: IUser[] = yield listExternalStudentsEffect();
      yield put(externalUsersSlice.actions.setResourses(findedUsers));  
    }
  );

  export const onToday = createCommand<void>(
    'onToday',
    function*() {
      const todayDate = new Date();
      yield put(teacherModalSlice.actions.setModalState({currentDate: todayDate}));

      yield updateExternalLessonsDateRangeEffect();
    }
  );

  export const onChengeDateForward = createCommand<void>(
    'onChengeDateForward',
    function*() {
      const currentDate: Date = yield select(selectCurrentDate);

      const newDate = addDays(currentDate, 7);
      yield put(teacherModalSlice.actions.setModalState({currentDate: newDate}));
      
      yield updateExternalLessonsDateRangeEffect();
    }
  );

  export const onChengeDateBackward = createCommand<void>(
    'onChengeDateBackward',
    function*() {
      const currentDate: Date = yield select(selectCurrentDate);

      const newDate = subDays(currentDate, 7);
      yield put(teacherModalSlice.actions.setModalState({currentDate: newDate}));      
      
      yield updateExternalLessonsDateRangeEffect();
    }
  );
