import { put } from "redux-saga/effects";
import { TeacherView, teacherModalSlice } from "../../store/modal";

export function* closeAbonementModalEffect() {
  yield put(teacherModalSlice.actions.setModalState({isAbonementModalOpen: false}));
}
export function* closeNewRuleModalEffect() {
  yield put(teacherModalSlice.actions.setModalState({isNewRuleModalOpen: false}));
}
export function* closeSubjectPageEffect() {
  yield put(teacherModalSlice.actions.setModalState({teacherView: TeacherView.subjectList}));
}
