import { ISelectOption } from "../../common/component/ChipsSelect";
import { IAbonementOffer, ITeacher, id } from "../../common/types/domain";
import { ISubjectView, ITeacherView } from "../../common/types/teacherDomainView";
import {ArgumentSelector} from "../../common/types/utility";
import { TeacherSelector } from "./types";

export const selectTeacherDomain: TeacherSelector<ITeacherView> = (state) => state.teacherDomain;
export const selectTeacherId: TeacherSelector<id> = (state) => selectTeacherDomain(state).id;
export const selectDomainTeacherName: TeacherSelector<string> = (state) => selectTeacherDomain(state).name;
export const selectDomainTeacherDescription: TeacherSelector<string> = (state) => selectTeacherDomain(state).description;
export const selectTeacherViewSubjects: TeacherSelector<ISubjectView[]> = (state) => selectTeacherDomain(state).subjects;
export const selectTeacherViewAbonementOffers: TeacherSelector<IAbonementOffer[]> = (state) => selectTeacherDomain(state).abonementOffers;

export const selectTeacherViewAbonementOffersById: ArgumentSelector<TeacherSelector<IAbonementOffer|undefined>> = (id: string) => {
  return (state) => selectTeacherViewAbonementOffers(state).find((offer)=>offer.id === id);
}

export const selectTeacherViewSubjectsById: ArgumentSelector<TeacherSelector<ISubjectView|undefined>> = (id: string) => {
  return (state) => selectTeacherViewSubjects(state).find((subject)=>subject.id === id);
}

export const selectTeacherSubjectsLength: TeacherSelector<number> = (state) => selectTeacherViewSubjects(state).length;
export const selectTeacherOffersLength: TeacherSelector<number> = (state) => selectTeacherViewAbonementOffers(state).length;

export const selectAbonementOffersSubjectsFormatted: TeacherSelector<Map<id, string>> = (state) => {
  const idNameMap = new Map<id, string>();
  const abonementOffers = selectTeacherViewAbonementOffers(state);
  const subjects = selectTeacherViewSubjects(state);

  abonementOffers.forEach((abonementOffer) => {
    const thisAbonementOfferSubjects = subjects.filter((subject) => abonementOffer.subjects.includes(subject.id));
    const thisAbonementOfferSubjectsNames = thisAbonementOfferSubjects.map((subject) => subject.name);
    const subjectsFormatted = thisAbonementOfferSubjectsNames.join(', ');
    idNameMap.set(abonementOffer.id, subjectsFormatted);
  });

  return idNameMap;
}

export const selectTeacherSubjectSelectOptions: TeacherSelector<ISelectOption[]> = (state) => {
  const subjects = selectTeacherViewSubjects(state);

  const subjectSelectOptions = subjects.map((subject) => {
    const subjectSelectOption: ISelectOption = {id: subject.id, title: subject.name};
    return subjectSelectOption;
  });

  return subjectSelectOptions;
};

export const selectSubjectRulesIdsBySubjectId: ArgumentSelector<TeacherSelector<id[]|undefined>> = (id: string) => {
  return (state) => {
    const subject = selectTeacherViewSubjectsById(id)(state);
    const rules = subject?.rules;
    const rulesIds = rules?.map((rule) => rule.id);
    return rulesIds;
  }
};

export const selectTeacherEntity: TeacherSelector<ITeacher> = (state) => {
  const teacherView = selectTeacherDomain(state);
  const {abonementOffers, subjects, ...rest} = teacherView;
  const teacher: ITeacher = {
    ...rest,
    abonementOffers: selectTeacherViewAbonementOffersIds(state),
    subjects: selectTeacherViewSubjectsIds(state),
  };
  return teacher;
};

export const selectTeacherViewSubjectsIds: TeacherSelector<id[]> = (state) => {
  const subjects = selectTeacherViewSubjects(state);
  const subjectsIds = subjects.map((subject) => subject.id);
  return subjectsIds;
};

export const selectTeacherViewAbonementOffersIds: TeacherSelector<id[]> = (state) => {
  const abonementOffers = selectTeacherViewAbonementOffers(state);
  const abonementOffersIds = abonementOffers.map((offer) => offer.id);
  return abonementOffersIds;
};
