import { ArgumentSelector } from "../../common/types/utility";
import {IExternalResourceState, IExternalResourceFilters } from "../../common/store/externalResourceSlice";
import { id, ILesson, IUser, LessonStatus } from "../../common/types/domain";
import { TeacherSelector } from "./types";

const selectExternalLessons: TeacherSelector<IExternalResourceState<ILesson>> = (state) => state.externalLessons;
export const selectExternalLessonsData: TeacherSelector<ILesson[]> = (state) => selectExternalLessons(state).data;
export const selectExternalLessonsIdArray: TeacherSelector<id[]> = (state) => selectExternalLessonsData(state).map((lesson)=>lesson.id);
export const selectLesson: TeacherSelector<ILesson> = (state) => state.lesson;
export const selectExternalLessonsFilters: TeacherSelector<Partial<IExternalResourceFilters>> = (state) => selectExternalLessons(state).filters;
export const selectExternalLessonsFiltersSearch: TeacherSelector<string> = (state) => selectExternalLessonsFilters(state).search || '';
export const selectLessonStatus: TeacherSelector<LessonStatus> = (state) => selectLesson(state).status as LessonStatus;

const selectExternalUsers: TeacherSelector<IExternalResourceState<IUser>> = (state) => state.externalUsers;
export const selectExternalUsersData: TeacherSelector<IUser[]> = (state) => selectExternalUsers(state).data;
export const selectExternalUsersFilters: TeacherSelector<Partial<IExternalResourceFilters>> = (state) => selectExternalUsers(state).filters;
export const selectExternalUsersFiltersSearch: TeacherSelector<string> = (state) => selectExternalUsersFilters(state).search || '';

export const selectLessonParticipants: TeacherSelector<IUser[]> = (state) => selectLesson(state).participants;

export const selectLessonPaticipantById: ArgumentSelector<TeacherSelector<IUser|undefined>> = (id: string) => {
  return (state) => selectLessonParticipants(state).find((user)=>user.id === id);
}

export const selectLessonId: TeacherSelector<id> = (state) => selectLesson(state).id;

export const selectAcceptedLessonParticipants: TeacherSelector<IUser[]> = (state) => { 
  const {participants, accepted} = selectLesson(state);
  const acceptedSet = new Set(accepted);
  const acceptedParticipants = participants.filter((participant) => acceptedSet.has(participant.id));
  return acceptedParticipants;
 }

export const selectDeclainedLessonParticipants: TeacherSelector<IUser[]> = (state) => { 
  const {participants, declained} = selectLesson(state);
  const declainedSet = new Set(declained); 
  const declainedParticipants = participants.filter((participant) => declainedSet.has(participant.id));
  return declainedParticipants;
 }

export const selectPendingLessonParticipants: TeacherSelector<IUser[]> = (state) => { 
  const {participants, accepted, declained} = selectLesson(state);
  const notPendingSet = new Set ([...accepted, ...declained]);
  const pendingParticipants = participants.filter((participant) => !notPendingSet.has(participant.id));
  return pendingParticipants;
 }

 export const selectLessonParticipantlistHasActionMenu: TeacherSelector<boolean> = (state) => {
  const lessonStatus = selectLessonStatus(state);
  const hasActionMenu = (lessonStatus === LessonStatus.inProcess) || (lessonStatus === LessonStatus.finished);
  return hasActionMenu;
 }

 export const selectLessonHasTeacherLessonNavigation: TeacherSelector<boolean> = (state) => {
  const lessonStatus = selectLessonStatus(state);

  const hasTeacherLessonNavigation = (lessonStatus === LessonStatus.inProcess) || 
  (lessonStatus === LessonStatus.finished) || (lessonStatus === LessonStatus.commingSoon);

  return hasTeacherLessonNavigation;
 };

export const selectExternalLessonsFiltersStartDate: TeacherSelector<Date> = (state) => selectExternalLessonsFilters(state).startDate || new Date();
export const selectExternalLessonsFiltersEndDate: TeacherSelector<Date> = (state) => selectExternalLessonsFilters(state).endDate || new Date();
