import { put, select } from "redux-saga/effects";
import { ISubjectView, ITeacherView } from "../../../common/types/teacherDomainView";
import { readTeacherViewDomainEffect } from "../../api/domain";
import { teacherDomainViewSlice } from "../../store/domainView";
import { 
  editableAbonementOfferSlice, 
  editableRuleSlice, 
  editableSubjectSlice, 
  editableTeacherSlice
} from "../../store/teacherEditables";
import { selectSubjectId } from "../../selectors/editable";
import { selectTeacherViewSubjectsById } from "../../selectors/domain";
import { id } from "../../../common/types/domain";
import { teacherModalSlice } from "../../store/modal";

export const updateTeacherDomainEffect = function*() {
  const teacherDomain: ITeacherView = yield readTeacherViewDomainEffect();
  yield put(teacherDomainViewSlice.actions.setDomainView(teacherDomain));
  yield put(editableTeacherSlice.actions.setEditingResource(teacherDomain));
}

export const resetEditableAbonementOfferStateEffect = function*() {
  yield put(editableAbonementOfferSlice.actions.reset());
}
export const resetEditableRuleStateEffect = function*() {
  yield put(editableRuleSlice.actions.reset());
}
export const resetEditableSubjectStateEffect = function*() {
  yield put(editableSubjectSlice.actions.reset());
}

export const resetModalStateEffect = function*() {
  yield put(teacherModalSlice.actions.resetModalState());
}

export const updateEditingSubjectEffect = function*() {
  const subjectId: id = yield select(selectSubjectId);
  const subject: ISubjectView = yield select(selectTeacherViewSubjectsById(subjectId));
  yield put(editableSubjectSlice.actions.setEditingResource(subject));
}
