import { call, select } from "redux-saga/effects";
import { id, ILesson, LessonStatus } from "../../../common/types/domain";
import { selectLessonId } from "../../selectors/external";
import { 
  addStudentToLessonApiCall,
  changeStatusOwnLessonApiCall, 
  fetchOwnDataBaseLessonApiCall, 
  removeStudentFromLessonApiCall, 
  requestChangeStatusStudentApiCall, 
  sentAnotherNotificationApiCall 
} from "./ownLessonApi";
import { IDataBaseLesson } from "../../../common/api/handelers";
export function* fetchOwnLessonEffect(id:id) {
  const data: IDataBaseLesson = yield call(fetchOwnDataBaseLessonApiCall, id);
  const date = data.date.toDate();
  const fullLesson: ILesson = {...data, date};
  return fullLesson;
}

export function* cancelOwnLessonEffect() {
  const id: id = yield select(selectLessonId);
  yield call(changeStatusOwnLessonApiCall, LessonStatus.canceled, id);

  const ownLesson: ILesson = yield fetchOwnLessonEffect(id);
  return ownLesson;
}

export function* finishOwnLessonEffect() {
  const id: id = yield select(selectLessonId);
  yield call(changeStatusOwnLessonApiCall, LessonStatus.finished, id);

  const ownLesson: ILesson = yield fetchOwnLessonEffect(id);
  return ownLesson;
}

export function* addStudenToLessonLessonEffect(id:id) {
  yield call(addStudentToLessonApiCall, id);

  const lessonId: id = yield select(selectLessonId);
  const ownLesson: ILesson = yield fetchOwnLessonEffect(lessonId);
  return ownLesson;
}

export function* requestStatusChangeEffect(id:id) {
  yield call(requestChangeStatusStudentApiCall, id);

  const lessonId: id = yield select(selectLessonId);
  const ownLesson: ILesson = yield fetchOwnLessonEffect(lessonId);
  return ownLesson;
}

export function* removeStudentFromLessonEffect(id:id) {
  yield call(removeStudentFromLessonApiCall, id);

  const lessonId: id = yield select(selectLessonId);
  const ownLesson: ILesson = yield fetchOwnLessonEffect(lessonId);
  return ownLesson;
}

export function* sentAnotherNotificationEffect(id:id) {
  yield call(sentAnotherNotificationApiCall, id);
}
