import { IExternalResourceFilters } from "../../../common/store/externalResourceSlice";
import { ILesson, ISubject, ITeacher, IUser, id } from "../../../common/types/domain";
import { domainHandelers } from "../../../common/api/domainApi";
import { IDataBaseLesson } from "../../../common/api/handelers";

export const listExternalStudentsApiCall = async ( filters:IExternalResourceFilters): Promise<IUser[]> => {
  const users = await domainHandelers.userAPIHandeler.list('', filters);
  return users;
}

export const listExternalTeachersApiCall = async (userId: id, filters:IExternalResourceFilters): Promise<ITeacher[]> => {
  const teachers = await domainHandelers.teacherAPIHandeler.list(userId, filters);
  return teachers;
}

export const listExternalLessonsApiCall = async (userId: id, filters:IExternalResourceFilters): Promise<ILesson[]> => {
  const dataBaseLessons = await domainHandelers.lessonAPIHandeler.list(userId, filters);
  const lessons = mapDataBaseLessonsToLessons(dataBaseLessons);
  return lessons;
}

export const listExternalSubjectsApiCall = async (userId: id, filters:IExternalResourceFilters): Promise<ISubject[]> => {

  const externalSubjects = await domainHandelers.subjectApiHandeler.list(userId, filters);
  return externalSubjects;
}

export const mapDataBaseLessonsToLessons = (dataBaseLessons: IDataBaseLesson[]): ILesson[] => {
  const result = dataBaseLessons.map((item) => {
    return {
      ...item,
      date: item.date.toDate(),
    }
  });

  return result;
};
