import { put, select } from "redux-saga/effects";
import { AbonementType, id, PeriodicityType } from "../../../common/types/domain";
import { createCommand } from "../../../common/utilities/createCommand";
import { selectAbonementOfferAbonementSubjects, selectIsTeacherDescriptionEditing, selectIsTeacherNameEditing, selectRulePeriodicityRule, selectRulePeriodicityType } from "../../selectors/editable";
import { 
  editableTeacherSlice, 
  editableSubjectSlice,
  editableAbonementOfferSlice,
  editableRuleSlice
 } from "../../store/teacherEditables";
import { toggleArrayValue } from "../../../common/commands/toggleArrayValue";
import { saveTeacherDescriptionEffect, saveTeacherNameEffect } from "../../api/domain";
import { teacherModalSlice } from "../../store/modal";

export const startChangeSubjectName = createCommand<void>(
  'startChangeSubjectName',
  function*() {
    yield put(editableSubjectSlice.actions.startEditingField('name'));
  }
); 
  
export const changeSubjectName = createCommand<string>(
  'changeSubjectName',
  function*({payload: subjectName}) {
    yield put(editableSubjectSlice.actions.setEditingResource({name: subjectName}));
  }
);

export const confirmChangeSubjectName = createCommand<void>(
  'confirmChangeSubjectName',
  function*() {
    yield put(editableSubjectSlice.actions.finishEditingField('name'));
  }
); 

export const startChangeTeacherName = createCommand<void>(
  'startChangeTeacherName',
  function*() {
    yield put(editableTeacherSlice.actions.startEditingField('name'));
  }
); 

export const changeTeacherName = createCommand<string>(
  'changeTeacherName',
  function*({payload: teacherName}) {
    yield put(editableTeacherSlice.actions.setEditingResource({name: teacherName}));
  }
);

export const confirmChangeTeacherName = createCommand<void>(
  'confirmChangeTeacherName',
  function*() {
    const isTeacherNameEditing: boolean = yield select(selectIsTeacherNameEditing);

    if ( isTeacherNameEditing ) {
      yield put(editableTeacherSlice.actions.finishEditingField('name'));
      yield saveTeacherNameEffect();
    }
  }
); 

