import { put, select } from "redux-saga/effects";
import { createCommand } from "../../../common/utilities/createCommand";
import { IAbonementOffer, id, IRule } from "../../../common/types/domain";
import { 
  resetEditableAbonementOfferStateEffect, 
  resetEditableRuleStateEffect, 
  resetEditableSubjectStateEffect, 
  resetModalStateEffect, 
  updateEditingSubjectEffect, 
  updateTeacherDomainEffect 
} from "./updateDomainEffect";
import { 
  saveSubjectEffect,
  saveAbonementEffect,
  saveRuleEffect,
  deleteAbonementEffect,
  deleteRuleEffect,
  deleteSubjectEffect
 } from "../../api/domain";
import { selectTeacherViewAbonementOffersById, selectTeacherViewSubjectsById } from "../../selectors/domain";
import { editableAbonementOfferSlice, editableRuleSlice, editableSubjectSlice } from "../../store/teacherEditables";
import { TeacherCalendarRepresentationType, teacherModalSlice, TeacherView } from "../../store/modal";
import { selectSubjectId } from "../../selectors/editable";
import { ISubjectView } from "../../../common/types/teacherDomainView";
import { closeAbonementModalEffect, closeNewRuleModalEffect, closeSubjectPageEffect } from "../navigation/effects";
import { listExternalLessonsEffect } from "../../api/external";
import { updateExternalLessonsDateRangeEffect } from "../external/updateExternalLessonsDateRangeEffect";
import { setTecherCalendarRepresentationType } from "../navigation/navigation";

export const startTeacherApp = createCommand<void>(
  'startTeacherApp',
  function*() {
    yield updateTeacherDomainEffect();
    yield updateExternalLessonsDateRangeEffect();
    yield listExternalLessonsEffect();
  }
);

export const startDeletingSubject = createCommand<id>(
  'startDeletingSubject',
  function*({payload: subjectId}) {
    yield deleteSubjectEffect(subjectId);
    yield updateTeacherDomainEffect();
  }
);
 
export const startDeletingAbonement = createCommand<id>(
  'startDeletingAbonement',
  function*({payload: abonementId}) {
    yield deleteAbonementEffect(abonementId);
    yield updateTeacherDomainEffect();
  }
); 

export const startDeletingRule = createCommand<id>(
  'startDeletingRule',
  function*({payload: ruleId}) {
    yield deleteRuleEffect(ruleId);
    yield updateTeacherDomainEffect();
    yield updateEditingSubjectEffect();
    yield closeNewRuleModalEffect();
    yield resetEditableRuleStateEffect();
  }
);

export const confirmSavingAbonementOffer = createCommand<void>(
  'confirmSavingAbonement',
  function*() {
    yield saveAbonementEffect();
    yield updateTeacherDomainEffect();
    yield closeAbonementModalEffect();
    yield resetEditableAbonementOfferStateEffect();
  }
);

export const confirmSavingSubject = createCommand<void>(
  'confirmSavingSubject',
  function*() {
    yield saveSubjectEffect();
    yield updateTeacherDomainEffect();
    yield resetModalStateEffect();
    yield closeSubjectPageEffect();
    yield resetEditableSubjectStateEffect();
  }
);

export const confirmSavingRule = createCommand<void>(
  'confirmSavingRule',
  function*() {
    yield saveRuleEffect();
    yield updateTeacherDomainEffect();
    yield updateEditingSubjectEffect();
    yield closeNewRuleModalEffect();
    yield resetEditableRuleStateEffect();
  }
);


export const startEditingSubject = createCommand<id>(
  'startEditingSubject',
  function*({payload: subjectId}) {
    const selectedSubject: ISubjectView = yield select(selectTeacherViewSubjectsById(subjectId));

    yield put(editableSubjectSlice.actions.setEditingResource(selectedSubject));

    yield put (setTecherCalendarRepresentationType.action(TeacherCalendarRepresentationType.calendarWeek));

    yield put(teacherModalSlice.actions.setModalState({teacherView: TeacherView.subjectPage}));
    }
);

export const startEditingAbonementOffer = createCommand<id>(
  'startEditingAbonementOffer',
  function*({payload: abonementId}) {
    const selectedAbonementOffer: IAbonementOffer = yield select(selectTeacherViewAbonementOffersById(abonementId));
    yield put(editableAbonementOfferSlice.actions.setEditingResource(selectedAbonementOffer));

    yield put(teacherModalSlice.actions.setModalState({isAbonementModalOpen: true}));
  }
);

export const startEditingRule = createCommand<id>(
  'startEditingRule',
  function*({payload: ruleId}) {
    const subjectId = yield select(selectSubjectId);
    const subjectView: ISubjectView = yield select(selectTeacherViewSubjectsById(subjectId));
    const selectedRule: IRule|undefined = subjectView.rules.find((rule)=>rule.id === ruleId);
    if (selectedRule) {
      yield put(editableRuleSlice.actions.setEditingResource(selectedRule));

      yield put(teacherModalSlice.actions.setModalState({isNewRuleModalOpen:true}));
    }
  }
);
