import { call, put, select } from "redux-saga/effects";
import { 
  IAbonementOffer, 
  id, 
  IRule, 
  ISubject, 
  ITeacher 
} from "../../../common/types/domain";
import { ISubjectView, ITeacherView } from "../../../common/types/teacherDomainView";
import { 
  selectAbonementOfferData, 
  selectEditingSubjectAsISubject, 
  selectRuleData, 
  selectSubjectData,  
  selectSubjectId,
  selectTeacherDescription,
  selectTeacherName
} from "../../selectors/editable";
import { 
  readTeacherViewApiCall, 
  domainHandelers,
} from "../../../common/api/domainApi";
import { selectAuthenticatedUserId } from "../../../common/selectors/commonSelectors";
import { selectSubjectRulesIdsBySubjectId, selectTeacherEntity } from "../../selectors/domain";
import { editableSubjectSlice } from "../../store/teacherEditables";
import { IDataBaseRule } from "../../../common/api/handelers";
import { CreateType } from "../../../common/api/types";
import { updateTeacherDomainEffect } from "../../commands/domain/updateDomainEffect";

export function* readTeacherViewDomainEffect() {
  const userId: id = yield select(selectAuthenticatedUserId);
  if (userId) {
    try {
      const data: ITeacherView = yield call(readTeacherViewApiCall, userId);
      return { ...data, id: userId };
    } catch (error) {
      console.log(error);
    }
  } else {
    console.warn('User is not authenticated');
  }
}

export function* saveSubjectEffect() {
  const userId: id = yield select(selectAuthenticatedUserId);
  const subject: ISubject =  yield select(selectEditingSubjectAsISubject);
  const subjectWithTeacherId: ISubject = {...subject, teacherId: userId};
  const { id, ...subjectToCreate } = subjectWithTeacherId;

  const isNew = !id;
  if (isNew) {
    const createdSubjectId: id = yield call(domainHandelers.subjectApiHandeler.create, subjectToCreate);
    yield addSubjectToTeachersSubjectsEffect(createdSubjectId);
  } else {
    yield call(domainHandelers.subjectApiHandeler.update, subjectWithTeacherId);
  }
}



export function* deleteSubjectEffect(id:id) {
  yield deleteSubjectRulesEffect(id);
  yield call(domainHandelers.subjectApiHandeler.delete, id);
}

function* deleteSubjectRulesEffect(id: id) {
  const rulesIds: id[] = yield select(selectSubjectRulesIdsBySubjectId(id));
  for (const ruleId of rulesIds) {
    yield call(deleteRuleEffect, ruleId);
  }
};

function* addSubjectToTeachersSubjectsEffect(subjectId: id) {
  const teacher: ITeacher = yield select(selectTeacherEntity);
  const newSubjects = [...teacher.subjects, subjectId];
  const newTeacher: ITeacher = {...teacher, subjects: newSubjects};
  yield call(domainHandelers.teacherAPIHandeler.update, newTeacher);
}

export function* saveAbonementEffect() {
  const userId: id = yield select(selectAuthenticatedUserId);
  const abonementOffer: IAbonementOffer = yield select(selectAbonementOfferData);
  const abonementOfferWithTeacherId: IAbonementOffer = {...abonementOffer, teacherId: userId}
  const {id, ...abonementOfferToCreate} = abonementOfferWithTeacherId;
  const isNew = !id;
  if (isNew) {
    const createdAbonementOfferId: id = yield call(domainHandelers.abonementOffer.create,  abonementOfferToCreate);
    yield addAbonementOfferToTeachersAbonementOffersEffect(createdAbonementOfferId);
  } else {
    yield call(domainHandelers.abonementOffer.update, abonementOffer);
  }
}

function* addAbonementOfferToTeachersAbonementOffersEffect(abonementOfferId: id) {
  const teacher: ITeacher = yield select(selectTeacherEntity);
  const newAbonementOffers = [...teacher.abonementOffers, abonementOfferId];
  const newTeacherView = {...teacher, abonementOffers: newAbonementOffers};
  yield call(domainHandelers.teacherAPIHandeler.update, newTeacherView as any);
}

export function* deleteAbonementEffect(id:id) {
  yield call(domainHandelers.abonementOffer.delete, id);
}

export function* saveRuleEffect() {
  const subjectId: id = yield select(selectSubjectId);
  const userId: id = yield select(selectAuthenticatedUserId);
  const rule: IRule = yield select(selectRuleData);

  const isNewSubject = !subjectId;

  if (isNewSubject) {
    const subject: ISubject = yield select(selectEditingSubjectAsISubject);
    const subjectWithTeacherId: ISubject = {...subject, teacherId: userId};
    const { id, ...subjectToCreate } = subjectWithTeacherId;
    const createdSubjectId: id = yield call(domainHandelers.subjectApiHandeler.create, subjectToCreate);
    
    yield addSubjectToTeachersSubjectsEffect(createdSubjectId);
    yield put(editableSubjectSlice.actions.setEditingResource({id: createdSubjectId, teacherId: userId}));
    const ruleWithIds: IRule = {...rule, subjectId: createdSubjectId, teacherId: userId}
    yield call(createOrUpdateRule, ruleWithIds);
  } else {
    const ruleWithIds: IRule = {...rule, subjectId: subjectId, teacherId: userId}
    yield call(createOrUpdateRule, ruleWithIds);
  }
}

function* createOrUpdateRule(ruleWithIds: IRule) {
  const {id, ...ruleToCreate} = ruleWithIds;
  const isNew = !id;
  if (isNew) {
    const createdRuleId: id = yield call(domainHandelers.rulesApiHandeler.create, ruleToCreate as unknown as CreateType<IDataBaseRule>);
    yield addRuleToSubjectRulesEffect(createdRuleId);
  } else {
    yield call(domainHandelers.rulesApiHandeler.update, ruleWithIds as unknown as IDataBaseRule);
  }
}

function* addRuleToSubjectRulesEffect(ruleId: id) {
  const subject: ISubjectView = yield select(selectSubjectData);
  const ruleIds = subject.rules?.map((rule) => rule.id) || [];
  const newRules = [...ruleIds, ruleId];
  const newSubject: ISubject = {...subject, rules: newRules }
  yield call(domainHandelers.subjectApiHandeler.update, newSubject );
}

export function* deleteRuleEffect(id:id) {
  yield call(domainHandelers.rulesApiHandeler.delete, id);
}

export function* saveTeacherDescriptionEffect() {
  const teacherDescription: string   = yield select(selectTeacherDescription);
  const teacher: ITeacher = yield select(selectTeacherEntity);
  const newTeacher: ITeacher = {...teacher, description: teacherDescription};
  
  yield call(domainHandelers.teacherAPIHandeler.update, newTeacher);
  yield updateTeacherDomainEffect();
}

export function* saveTeacherNameEffect() {
  const teacherName: string   = yield select(selectTeacherName);
  const teacher: ITeacher = yield select(selectTeacherEntity);
  const newTeacher: ITeacher = {...teacher, name: teacherName};
  
  yield call(domainHandelers.teacherAPIHandeler.update, newTeacher);
  yield updateTeacherDomainEffect();
}
