import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ILesson, LessonStatus } from "../../../common/types/domain";

export interface ILessonState extends ILesson{};

const lessonlInitialState: ILessonState = {
  id:"",
  date: new Date(),
  participants:[],
  durationMin: 0,
  status: LessonStatus.scheduled,
  accepted: [],
  declained:[],
  subject: {
    id: '',
    name: '',
    description: '',
    rules: [],
    teacherId: ''
  },
  teacherId: '',
};

export const lessonSlice = createSlice({
  name: 'lesson',
  initialState: lessonlInitialState,
  reducers: {
    setLessonState: (state, { payload: stateDiff }: PayloadAction<Partial<ILesson>>) => ({
      ...state,
      ...stateDiff,
    }),
    resetLessonState: () => ({...lessonlInitialState}),
  },
});
